<template>
  <div class="wrapper wrapper-content">
    <div class="ibox float-e-margins">
      <div class="ibox-title">
        <h5>公告详情</h5>
      </div>
      <div class="ibox-content">
        <form class="form-horizontal" @submit.prevent="save">
          <div class="row">
            <template v-if="!readonly">
              <div class="form-group">
                <label class="col-sm-2 control-label"><span class="MustFill">*</span>标题</label>
                <div class="col-sm-4">
                  <input type="text" class="form-control" v-model="form.name" v-if="!readonly">
                  <p v-if="readonly">{{ form.name }}</p>
                </div>
              </div>

              <div class="form-group">
                <label class="col-sm-2 control-label">展示图片</label>
                <div class="col-sm-4">
                  <button type="button" class="btn btn-sm btn-primary"><i class="fa fa-upload"></i>&nbsp;&nbsp;
                    <span class="bold">选择图片</span>
                  </button>
                  <input id="showImg" type="file" class="file" @change="fileChange()">
                  (首页轮播展示图,查看不展示)
                </div>
              </div>

              <div class="form-group">
                <div class="col-sm-2"></div>
                <div class="col-sm-4">
                  <img :src="imgUrl" class="showImg">
                </div>
              </div>

              <div class="form-group">
                <label class="col-sm-2 control-label">附件</label>
                <div class="col-sm-4">
                  <button type="button" class="btn btn-sm btn-primary"><i class="fa fa-upload"></i>&nbsp;&nbsp;
                    <span class="bold">选择文件</span>
                  </button>
                  <input id="file" type="file" class="file" @change="fileChange(1)">
                  {{ annexUrl }}
                </div>
              </div>
            </template>

            <template v-else>
              <label class="col-sm-2 control-label"></label>
              <div class="col-sm-10">
                <h1 class="">{{ form.name }}</h1>
                <h6>{{ form.created_at }}</h6>
              </div>

              <div class="form-group" v-if="form.annex">
                <label class="col-sm-2 control-label">附件</label>
                <div class="col-sm-4">
                  <button type="button" class="btn btn-sm btn-primary"><i class="fa fa-download"></i>&nbsp;&nbsp;
                    <span class="bold">下载附件</span>
                  </button>
                  <input id="download" class="file" @click="download()">
                </div>
              </div>
            </template>


            <div class="form-group">
              <label class="col-sm-2 control-label">
                <template v-if="id == 'new'">正文</template>
              </label>
              <div class="col-sm-10">
                <div id="eg">
                  <div class="click2edit wrapper" v-html="form.detail"></div>
                </div>
              </div>
            </div>


          </div>
        </form>



        <div class="text-center btns">
          <template v-if="!readonly">
            <button class="btn btn-info" @click="edit()">编辑正文HTML</button>
            <button v-if="canSubmit" type="submit" class="btn btn-primary" @click="save()">保存</button>
            <button v-if="!canSubmit" class="btn btn-default">保存</button>
          </template>
          <b class="btn btn-white" @click="cancel">取消</b>
        </div>

      </div>
    </div>

    <div style="display: none">{{ count }}</div>
  </div>
</template>

<script type="text/ecmascript-6">
import { API_URL } from '@/assets/config'
import { NoticeKeys } from '@/assets/constant'
import dataService from '@/assets/method/data'
import tabsService from '@/assets/template/tabs'
import Service from '@/assets/service/service'
export default {
  name: 'noticeDetails',
  data () {
    return {
      id: this.$route.params['id'],
      type: this.$route.query['type'],
      readonly: this.$route.query['readonly'],
      dataKeys: NoticeKeys,
      form: {},
      canSubmit: true,
      html: '',
      imgUrl: '',
      annexUrl: '',
      count: 0
    }
  },
  methods: {
    download () {
      const files = {}
      files['$form'] = $('<form method="GET" id="form"></form>')
      files['$form'].attr('action', API_URL + this.form.annex)
      files['$form'].appendTo($('body'))
      files['$form'].submit()
      this.count++
    },
    fileChange (isFile) {
      if (isFile) {
        if ($('#file').get(0).files[0]) {
          this.form['annex'] = $('#file').get(0).files[0]
          this.annexUrl = this.form['annex'].name
          this.count++
        }
      } else {
        if ($('#showImg').get(0).files[0]) {
          this.form['pic'] = $('#showImg').get(0).files[0]
          const reader = new FileReader();
          reader.readAsDataURL($('#showImg').get(0).files[0]);
          reader.onload = e => {
            this.imgUrl = e.target.result;
          }
          this.count++
        }
      }
    },
    edit () {
      $("#eg").addClass("no-padding");
      $('.click2edit').summernote({
        lang: 'zh-CN',
        focus: true
      });
    },
    save () {
      if (!this.canSubmit) return
      if (!this.form.name) {
        toastr.warning('请填写标题')
        return
      }
      this.canSubmit = false
      $("#eg").removeClass("no-padding")
      $('.click2edit').destroy()
      const request = {
        type: this.type,
        detail: $('.click2edit').code(),
        name: this.form.name,
        key: this.form.key,
        pic: this.form['pic'],
        annex: this.form['annex']
      }
      this.form['type'] = this.type
      this.form['detail'] = $('.click2edit').code()
      dataService.getFormVal(this.dataKeys, request).then(result => this.confirm(result))
    },
    confirm (request) {
      if (this.id != 'new') {
        Service.Notice.edit(this.id, request).then(response => this.responseHandle(response))
      } else  {
        Service.Notice.add(request).then(response => this.responseHandle(response))
      }
    },
    responseHandle (response) {
      if (response.code == 200) {
        toastr.success(response.msg)
        this.canSubmit = true
        setTimeout(() => dataService.tabReload(501), 1000)
      } else {
        toastr.error(response.msg)
        this.canSubmit = true
      }
    },
    cancel () {
      tabsService.closeNowTab()
    }
  },
  created () {
    if (this.id != 'new') {
      Service.Notice.details(this.id).then(response => {
        if (response.code == 200) {
          this.form = response.data
          this.html = this.form.detail
          if (response.data.pic) {
            this.imgUrl = API_URL + response.data.pic
          }
        }
      })
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
  .showImg {
    max-width: 500px;
  }
  .btns {
    display: inline-block;
    width: 100%;
    margin-top: 15px;
  }
  #eg {
    border-top: 1px dashed #999;
    margin-top: 15px;
    padding-top: 15px;
  }
  p {
    line-height: 34px;
    margin-bottom: 0;
  }
  .details {
    padding: 20px;
  }
</style>



